// global.js
import { createGlobalStyle } from 'styled-components';

export const GlobalStyles = createGlobalStyle`
  html {
    box-sizing: border-box;
    overflow-y: hidden;
  }

  html, body {
    margin: 0;
    padding: 0;
    overflow-y: inherit;
  }
  *, *::after, *::before {
    box-sizing: inherit;
  }
  body {
    background: ${({ theme }) => theme.primaryLight};
    color: ${({ theme }) => theme.primaryDark};
    font-family: 'Nunito Sans', 'sans-serif';

    align-items: center;
    display: flex;
    justify-content: center;
    text-rendering: optimizeLegibility;

    padding: 32px;
  }
  h1 {
    margin: 0;
    padding: 0;
    font-size: 3.5rem;
    text-transform: uppercase;
    display: block;
  }
  h2 {
    margin: 0;
    padding: 0;
    font-size: 2rem;
    text-transform: uppercase;
    display: block;
  }
  h3 {
    margin: 0;
    padding: 0;
    font-size: 1.5rem;
    text-align: center;
  }

  table: {
    margin: 0;
    padding: 0;
    text-align: center;
    display: block;
  }

  img {
    height: auto;
    width: 15rem;
    margin-top: 2rem;
  }

  div {
    text-align: center;
  }

  small {
    display: block;
  }

  a {
    color: #343078;
    text-decoration: none;
  }
  `