// hooks.js
import { useEffect } from 'react';

export const useOnClickOutside = (ref, handler) => {
  useEffect(() => {
    const listener = event => {
      if (!ref.current || ref.current.contains(event.target)) {
        return;
      }
      /* placeholder function? maybe */
      handler(event);
    };

    /* 
     * What this does is capture any mouse down event and trigger the menu to close.
     * It just doesn't get this far if you click inside the menu, because the function
     * will return before that.
     */
    document.addEventListener('mousedown', listener);
    return () => {
      document.removeEventListener('mousedown', listener);
    };
  },
  [ref, handler],
  );
};