// Burger.styled.js
import styled from 'styled-components';

/* I guess we are styling a button */
export const StyledBurger = styled.button`

  /* Configure button positioning to be at the top-left of the screen */
  position: fixed;
  top: 5%;
  right: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 2rem;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 10;
  
  &:focus {
    outline: none;
  }
  
  /* Styling of the actual burger layer */
  div {
    width: 2rem;
    height: 0.25rem;
    background: ${({ theme, menu_open }) => menu_open ? theme.primaryLight : theme.primaryDark};
    border-radius: 10px;
    transition: all 0.3s ease-out;
    position: relative;
    transform-origin: 1px;

    :nth-child(1) {
      transform: ${({ menu_open }) => menu_open ? 'rotate(45deg)' : 'rotate(0)'};
    }

    :nth-child(2) {
      opacity: ${({ menu_open }) => menu_open ? '0' : '1'};
      transform: ${({ menu_open }) => menu_open ? 'translateX(-2rem)' : 'translateX(0)'};
    }

    :nth-child(3) {
      transform: ${({ menu_open }) => menu_open ? 'rotate(-45deg)' : 'rotate(0)'};
    }
  }
`;