// App.js
import React, { useState, useRef } from 'react';
import { HashRouter as Router, Route, Switch, NavLink } from 'react-router-dom';
import { useOnClickOutside } from './hooks';
import { ThemeProvider } from 'styled-components';
import { GlobalStyles } from './global';
import { theme } from './theme';
import { Burger, Menu } from './components';
import { Header } from './Header.styled';

import ProfilePic from './profile.png';

function App() {

  const [menu_open, menu_setOpen] = useState(false);

  const node = useRef();
  useOnClickOutside(node, () => menu_setOpen(false));
  
  return (
    <Router>
      <ThemeProvider theme={theme}>
        <GlobalStyles />
        <Header>
          <ul>
            <li><h3>Alan Wright</h3></li>
            <li><NavLink exact to="/">Home</NavLink></li>
            <li><NavLink to="/Projects">Projects</NavLink></li>
            <li><NavLink to="/Contact">Contact</NavLink></li>
          </ul>
        </Header>
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/Projects">
            <Projects />
          </Route>
          <Route path="/Contact">
            <Contact />
          </Route>
        </Switch>
        
        {/*<div ref={node}>
          <Burger menu_open={menu_open} menu_setOpen={menu_setOpen} />
          <Menu menu_open={menu_open} menu_setOpen={menu_setOpen} />
        </div>*/}
      </ThemeProvider>
    </Router>
  );
}

function Home() {
  return (
    <div>
      <img src={ProfilePic} alt="Profile Picture" />
      <h1>Developer &#183; Designer</h1>
      <h2>Web / UX / Games</h2>
      <hr />
      <div style={{'max-width':'512px', 'margin':'auto'}}>
        <h3>C++ | HTML/CSS | Javascript | NodeJS | React | ExpressJS | GIMP 2 | Krita | Blender2D | Inkscape | Java | Python | C | C#</h3>
      </div>
    </div>
  );
}

function Projects() {
  return (<h2>Projects</h2>);
}

function Contact() {
  return (<h2>Contact</h2>);
}

export default App;