// Header.styled.js
import styled from 'styled-components';

export const Header = styled.nav`
  
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 0ch;
  text-align: center;

  h3 {
    margin: 0;
    padding: 0;
  }

  ul {
    width: 100vw;
    max-width: 900px;
    height: 96px;
    background-image: linear-gradient(#ffffff00, #ffffffe0, #ffffffe0, #ffffff00);
    list-style: none;
    display: table;
    margin-left: auto;
    margin-right: auto;
    margin-top: -16px;
    padding-left: 0;
    padding-right: 32px;
    padding-top: 32px;
  }

  li {
    display: table-cell;
    text-align: center;
  }
`;
